import React, { Component } from 'react'
import { Row, Col, Form, Button, Dropdown } from 'react-bootstrap';
import jwtDecode from 'jwt-decode'
import _ from 'lodash'
import { hostUrl } from '../../store/config';

class Bio extends Component {

    constructor(props)
    {
        super(props);

        this.state={
            user:null,
            profileForm: false,
        }
    }

    static getDerivedStateFromProps(props, state) {
        return {
            user: props.user,
            Profilemsg: props.Profilemsg,
            variant: props.variant,
            profileForm: props.profileForm
        }
    }

    handleProfileChange = (event) => {
        let updatedUser = this.state.user;

        switch(event.target.name)
        {
            case 'name':
                updatedUser.cust_name = event.target.value;
                console.log('Name :: ' + event.target.value);
            break;

            case 'iam':
                updatedUser.cust_gender =  event.target.value;
                console.log('Gender :: ' + event.target.value);
            break;

            case 'image':
                updatedUser.cust_image = event.target.files[0];
            break;
        }

        this.setState({
            user: updatedUser,
            msg: 'Photo selected, Press save to upload'
        })


    }


    handleSubmit = () => {

        let formBody = new FormData();
    
        if(localStorage.getItem('jwtToken'))
        {
            let cust_id = (jwtDecode(localStorage.getItem('jwtToken'))).cust_id;
            formBody.append('cust_id',cust_id)
        }
        formBody.append('cust_name', this.state.user.cust_name);
        formBody.append('cust_gender', this.state.user.cust_gender);
        
        if(typeof this.state.user.cust_image !== 'string')
        {
            console.log('Triggered');
            formBody.append('image', this.state.user.cust_image);
        }
        this.props.submitBio(formBody);

        this.setState({
            image:null,
            msg:'',
            profileForm: false
        })
    }

    render() {

        let user = null;
        if(this.state.user)
        user = this.state.user;

        return (
            <div id="profile">
                <Row className="m-0">
                    <Col md={7}>
                        <div className={this.state.profileForm === true ? "form show" : "form hide"}>
                            <Form
                            >
                                <Form.Group as={Row} controlId="name" className="mb-4">
                                    <Form.Label column sm={2}>
                                        Name:
                                    </Form.Label>
                                    <Col sm={10}>
                                    <Form.Control type="text" name="name" placeholder="Name" 
                                                    value={(this.state.user && this.state.user.cust_name)? this.state.user.cust_name : '' } 
                                                    onChange={this.handleProfileChange}/>
                                    </Col>
                                </Form.Group>
                                <Form.Group as={Row} className="iam" controlId="iam">
                                    <Form.Label column sm={2}>
                                    I am:
                                    </Form.Label>
                                    <Col sm={8}>
                                        <Form.Group as={Row} onChange={this.handleProfileChange}> 
                                            <Col sm={5}>
                                            {(user && user.cust_gender && user.cust_gender == 'Female') ?    
                                                <Form.Check
                                                        type="radio"
                                                        label=" A Mom "
                                                        name="iam"
                                                        value="Female"
                                                        checked
                                                        id="formHorizontalRadios1"
                                                    />:
                                                    <Form.Check
                                                        type="radio"
                                                        label=" A Mom "
                                                        value="Female"
                                                        name="iam"
                                                        id="formHorizontalRadios1"
                                                    />}
                                            </Col>
                                            <Col sm={5}>
                                            {(user && user.cust_gender && user.cust_gender == 'Male') ? 
                                                <Form.Check
                                                    type="radio"
                                                    label=" A Dad "
                                                    name="iam"
                                                    value="Male"
                                                    checked
                                                    id="formHorizontalRadios2"
                                                />:
                                                <Form.Check
                                                    type="radio"
                                                    label=" A Dad "
                                                    name="iam"
                                                    value="Male"
                                                    id="formHorizontalRadios2"
                                                    />}
                                            </Col>
                                            <p style={{color:`${this.state.variant}`}} >{this.state.Profilemsg}</p>
                                            <Col sm={12}>
                                                <Button 
                                                    onClick={this.handleSubmit}
                                                        ref={btn => this.submitButton = btn} className="btn-submit">Save</Button>
                                            </Col>
                                        </Form.Group>
                                    </Col>
                                </Form.Group>
                            </Form>
                        </div>
                        <div className={(this.state.profileForm === true) ? "detail-list hide" : "detail-list show"}>
                            <div>
                                <span className="name">{(user && user.cust_name) ? user.cust_name: ''}</span>
                                <span className="relation">{(user && user.cust_gender) ? user.cust_gender : ''}</span>
                            </div>
                        </div>
                    </Col>
                    <Col md={5}>
                        <div className="text-left">
                            <div className="image-container">
                                <img width='100%' src={(user && user.cust_image) ?  `${hostUrl}${user.cust_image}`:'/assets/images/default-placeholder.png'} alt=""/>
                                <span 
                                    onClick={
                                        () => {
                                            this.props.toggleBio()
                                        }
                                    } 
                                    className={this.state.profileForm === true ? "bg-edit hide" : "bg-edit show"}
                                ></span>
                            </div>
                            <div className={this.state.profileForm === true ? "upload-btn-wrapper show" : "upload-btn-wrapper hide" }>
                                <button className="btn-upload">Upload Photo</button>
                                <p>{this.state.msg}</p>
                                <input
                                    onChange={this.handleProfileChange}
                                    type="file" name="image"
                                />
                            </div>
                            
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}


export default Bio;
